
.lmj-shopping-list {
	width: 100%;
}

.lmj-plant-list {
	list-style-type: none;
	padding: 32px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}


.lmj-sales {
	position: absolute;
	right: 0px;
	top: 0.625rem;
	font-weight: 500;
	color: white;
	padding: 0.313rem 0.625rem;
	background-color:#add139;
}

.btn-add-cart {
	
	width: 7rem;
	position: absolute;
	margin-top: -4.3rem;
	margin-left: 6rem;
	border: 1px solid#71ae41;
	border-radius: 9px;
	padding: 0.2rem;
	background-color: white;
	color:#71ae41;
	transition-duration: 0.4s;
	cursor: pointer;

}

.btn-add-cart:hover {
	color:white;
	background-color: #71ae41;
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}