.lmj-cart {
	color: white;
	background-color: #6dac3c;
	border-radius: 1.563rem;
	margin-left: 1rem;
	padding: 2rem;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	width: 18.75rem;
}

.far {
	cursor: pointer;
}

.lmj-cart-add-button {
	margin-left: 2rem;
	cursor: pointer;
}

.lmj-cart-toggle-button-cart {
	font-size: 3.125rem;
	color:red;
	background: none;
	outline: none;
	border: none;
	cursor: pointer;
	align-self: flex-end;
}

.lmj-cart-toggle-button-close {
	font-size: xx-large;
	background: none;
	outline: none;
	border: none;
	cursor: pointer;
	padding: 0;
	color: rgb(216, 216, 216);
	transition: 0.3s;
	align-self: flex-end;
}

.lmj-cart-toggle-button-close:hover {
	color: rgb(255, 255, 255);
}

.lmj-cart-closed {
	padding: 2rem;
}

#cart {
	color:#6dac3c;
	transition: ease-out 0.4s;
}

.btn-cart {
	width: 9rem;
	margin-left: auto;
	margin-right: auto;
	border: 1px solid#71ae41;
	border-radius: 9px;
	padding: 0.2rem;
	background-color: white;
	color:#71ae41;
	transition-duration: 0.4s;
	cursor: pointer;


}

.btn-cart:hover {
	color:rgb(0, 0, 0);
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

#cart:hover {
	color:#add139;
}

.cart-row {
	font-size: large;
	text-transform: capitalize;
}


/* icones */

.fa-trash-alt {
	margin-right: 1rem;
	color:white;
	transition-duration: 500ms;
}
.fa-trash-alt:hover {
	color:rgb(198, 18, 18);
}

.fa-minus-square {
	color: white;
	transition-duration: 500ms;
}
.fa-minus-square:hover {
	color:rgb(255, 177, 41);
}

.fa-plus-square {
	color: white;
	transition-duration: 500ms;
}
.fa-plus-square:hover {
	color:lawngreen;
}

@media screen and (max-width: 576px)
 {

	.lmj-cart {
		border-radius: 1.563rem;
		margin-left: auto;
		margin-right: auto;
		width: 16.75rem;
	}

	.lmj-cart-toggle-button-cart {
		font-size: 2.125rem;
		
	}

	.lmj-cart-closed {
		text-align: end;
	}
	


}