.lmj-banner {
	display: flex;
	flex-direction: row;
	justify-content: center;
	color: black;
	padding-top: 3rem;
	border-bottom: solid 2px black;
	align-items: center;
}

.lmj-title {
	padding-left: 1rem;
	padding-right: 1rem;
}

.lmj-logo {
	width: 4rem;
	padding-bottom: 1rem;
}

.recommandation{
	display: flex;
	flex-direction: row;
	justify-content: center;
	padding-right: 1rem;
	padding-left: 1rem;
}

@media screen and (max-width: 676px)
 {
	.recommandation{
		font-size: smaller;
	}

	.lmj-title {
		font-size: x-large;
	}

		
	.lmj-logo {
		width: 3rem;
		padding-bottom: 1rem;
	}
}