.lmj-plant-item-cover {
	margin-top: 1rem;
	margin-right: auto;
	margin-left: auto;
	height: 16rem;
	width: 14rem;
	border-radius: 20px;
}

.lmj-plant-item {

	width: 15rem;
	height: 28rem;
	margin: 2rem;
	display: flex;
	align-items: flex-start;
	flex-direction: column;
}

.item-title {
	text-transform: capitalize;
	margin-top: 0.2rem;
	margin-left: 1rem;
	font-weight: 500;
}

.item-desc {
	text-align: left;
	font-size: 0.8rem;
	margin-top: 0rem;
	padding-left: 1rem;
	padding-right: 1rem;
}

.top-mention {
	position: absolute;
	font-weight: 500;
	margin-top: 1.4rem;
	margin-left: 11.8rem;
	background-color:#add139;
	border-radius: 15px 0px 15px 15px;
}

.promo-text {
	color:white;
	padding: 0.3rem;

}
