.lmj-categories {
	text-align: center;
	padding-top: 30px;
}

.lmj-categories-select {
	margin-right: 15px;
	border: 1px solid#71ae41;
	height: 1.5rem;
	border-radius: 9px;
	cursor: pointer;
}
.lmj-categories-select:hover {
	
	border: 1px solid#afbda3;
	
}

.lmj-categories-select:active {
	border: 2px solid#71ae41;

}

.btn-cat {
	width: 9rem;
	height: 1.5rem;
	margin-left: auto;
	margin-right: auto;
	border: 1px solid#71ae41;
	border-radius: 9px;
	padding: 0.2rem;
	background-color: white;
	color:#71ae41;
	transition-duration: 0.4s;
	cursor: pointer;

}

.btn-cat:hover {
	color:rgb(255, 255, 255);
	background-color: #71ae41;
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
